import React from 'react';
import PropTypes from 'prop-types';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import Spinner from '../common/Spinner/Spinner';
import CryptoNavItemsContainer from '../WalletNavItems/CryptoNavItemsContainer';
import FiatNavItemsContainer from '../WalletNavItems/FiatNavItemsContainer';
import DepositReferenceIdModalContainer from '../Retail/RetailSidePanes/RetailFiatDeposit/DepositReferenceIdModalContainer';

import './WalletDetails.css';

const baseClasses = getBEMClasses('wallet-details');

const WalletDetailsComponent = (props, context) => {
  const {
    iconFileName,
    Product,
    ProductFullName,
    AvailableBalance,
    Hold,
    PendingDeposits,
    Amount,
    ProductType,
    ProductId
  } = props.positionDetails;
  const isCrypto = ProductType === 'CryptoCurrency';

  const renderPositionDetails = (label, value) => (
    <div className={baseClasses('detail')}>
      <div className={baseClasses('label')}>{context.t(label)}</div>
      <div className={baseClasses('value')}>{value}</div>
    </div>
  );

  return (
    <div className={baseClasses()}>
      {props.fetching ? (
        <Spinner isInline customClass={baseClasses} />
      ) : (
        <React.Fragment>
          <div className={baseClasses('product-actions-row')}>
            <div className={baseClasses('product-label')}>
              <div className={baseClasses('product-icon')}>
                <ProductIconContainer iconFileName={iconFileName} size={48} />
              </div>
              <div className={baseClasses('product-symbol-name')}>
                {`${Product} ${ProductFullName}`}
              </div>
            </div>
            <div className={baseClasses('wallet-nav-items')}>
              {isCrypto ? (
                <CryptoNavItemsContainer
                  product={{
                    iconFileName,
                    ProductFullName,
                    ProductSymbol: Product,
                    ProductId
                  }}
                />
              ) : (
                <FiatNavItemsContainer
                  product={{
                    iconFileName,
                    ProductFullName,
                    ProductSymbol: Product,
                    ProductId
                  }}
                />
              )}
            </div>
          </div>
          <div className={baseClasses('position-row')}>
            {renderPositionDetails('Available Balance', AvailableBalance)}
            {renderPositionDetails('Hold', Hold)}
            {renderPositionDetails('Pending Deposits', PendingDeposits)}
            {renderPositionDetails('Total Balance', Amount)}
          </div>
          <DepositReferenceIdModalContainer />
        </React.Fragment>
      )}
    </div>
  );
};

WalletDetailsComponent.propTypes = {
  positionDetails: PropTypes.object,
  fetching: PropTypes.bool.isRequired
};

WalletDetailsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default WalletDetailsComponent;



