import { connect } from 'react-redux';
import WalletComponent from './WalletDetailsComponent';
import {
  fetchingSelector,
  productPositionAssetsSelector
} from '../../redux/selectors/productPositionSelectors';

const mapStateToProps = state => {
  const { position } = state;
  const fetching = fetchingSelector(state);
  const positionDetails = productPositionAssetsSelector(state).find(
    i => i.ProductId === position.selectedProductId
  );

  return { positionDetails, fetching };
};

export default connect(mapStateToProps)(WalletComponent);



